.top-container {
  overflow: hidden;
  background: #135a58;
  font-weight: bold;
  color: white;
}
.top-container span a {
  color:white;
  text-decoration: none;
}
.logo{
  width : 145px;
  height: 88px;
}
.navbar{
background-color: white;
}
#collapsibleNavbar{
  margin: 0% 5% 0% 0%;
}
ul li{
  font-size: 14px;
  
  text-transform: uppercase;
padding: 2px 5px 2px 5px;
font-weight: bold;
}
ul li a {
  color: #585656 !important;
  background-color: transparent;
  outline: none;
  margin-bottom: -2px;
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  font-size: 14px;
  font-weight: bold;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}
.dropdown:hover .dropdown-content {
  display: block;
}

.bannerImg{
  padding-top: 12.5%;
  width: 100%;
}

.automobile h2{
  color: #135a58;
}
.automobileImg{
  border-radius: 20px;
  opacity: 0.5;
  cursor: pointer;
}
.automobileImg:hover{
  opacity: 1; 
}
.servicesImg{
  border: 1px solid #9e9c9c;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    text-align: center;
    margin: 0 auto;
    vertical-align: middle;
    line-height: 66px;
    padding: 1em;
    box-shadow: 0px 0px 8px -2px #313131;

}
