.img-hover-zoom {
  height: 300px; 
  overflow: hidden;
}

.img-hover-zoom img {
  transition: transform .5s ease;
}

.img-hover-zoom:hover img {
  cursor: pointer;
  box-shadow: 0 0 10px #ccc;
  background: black;
  transform: scale(1.2);
  border: 1px solid #777;
  opacity: 0.7;
}